import { mapState, mapMutations, mapActions } from 'vuex'
import { upyunUrl } from '../../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      telVal: '',
      codeVal: '',
      sTopTime: null,
      sendStatus: false,
      sec: 60,
      codeName: '获取验证码',
      sessionKey: '',
      needCode: 0
    }
  },
  onLoad () {
    this.getWxloginFn()
    clearInterval(this.sTopTime)
  },
  computed: {
    ...mapState([
      'userInfo',
      'bingPhoneInfo'
    ])
  },
  methods: {
    ...mapMutations({
      setBingPhoneInfo: 'SET_BING_PHONE_INFO'
    }),
    ...mapActions([
      'getWxlogin',
      'wxaes',
      'getCode',
      'bindingMobile'
    ]),
    // 获取用户的openID和sessionKey
    getWxloginFn () {
      const that = this
      uni.login({
        success: res => {
          const data = {
            that,
            code: res.code
          }
          that.getWxlogin(data).then((res) => {
            that.sessionKey = res.session_key
          })
        }
      })
    },
    // 获取用户的openID和sessionKey
    getPhoneNumber (e) {
      const data = {
        that: this,
        encryptedData: e.mp.detail.encryptedData,
        iv: e.mp.detail.iv,
        sessionKey: this.sessionKey
      }
      this.wxaes(data).then((res) => {
        if (res.ret === 1) {
          this.telVal = res.data.phoneNumber
          this.onBoundFn(false)
        }
      })
    },
    // 获取验证码
    onGetCodeFn () {
      const myreg = /^1\d{10}$/
      const telVal = this.telVal
      const data = {
        that: this,
        telVal,
        codeVal: ''
      }
      if (!myreg.test(telVal)) {
        this.showToastFn('手机号格式不正确!')
        return false
      }
      uni.showLoading({ title: 'Loading' })
      this.getCode(data).then((res) => {
        if (res.ret === 1) {
          this.computedCountDownFn()
          this.showToastFn(res.message)
        } else {
          this.showToastFn(res.message)
        }
      })
    },
    computedCountDownFn () {
      this.sendStatus = true
      this.sTopTime = setInterval(() => {
        if (this.sec > 0) {
          this.sec = this.sec - 1
        } else {
          this.sendStatus = false
          this.codeName = '重新获取'
          this.sec = 60
          clearInterval(this.sTopTime)
        }
      }, 1000)
    },
    // 绑定手机号码
    onBoundFn (isCode) {
      isCode ? this.needCode = 1 : this.needCode = 0

      if (isCode && this.bingPhoneInfo.status) {
        this.bingPhoneInfo.status = 0
      } else {
        const myreg = /^1\d{10}$/
        const telVal = this.telVal
        const codeVal = this.codeVal
        const data = {
          that: this,
          telVal,
          codeVal,
          type: this.needCode
        }
        if (isCode && !myreg.test(telVal)) {
          this.showToastFn('手机号格式不正确!')
          return false
        }
        if (isCode && !codeVal) {
          this.showToastFn('请填写验证码!')
          return false
        }
        uni.showLoading({ title: 'Loading' })
        this.bindingMobile(data).then((res) => {
          if (res.ret === 1) {
            this.setBingPhoneInfo({ status: 1, mobile: telVal })
            this.emptyDataFn()
          }
          this.showToastFn(res.message)
        })
      }
    },
    // 清空数据
    emptyDataFn () {
      this.telVal = ''
      this.codeVal = ''
      this.sendStatus = ''
      this.sec = 60
      this.codeName = '获取验证码'
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 1500
      })
    }
  },
  onUnload () {
    this.emptyDataFn()
  }
}
